@import "~admin-stylesheets/colors";

.nab-heatmap-kind-selector {
	display: block;

	.components-tab-panel__tabs {
		background: $color__background-grey;
		border-top: 0;
		display: flex;
		padding-left: 0;
		top: 0;
		margin-top: 0;

		.components-button {
			border-radius: 0;
			height: 50px;
		}

		.components-button:focus:not(:disabled) {
			background-color: $color__background-grey;
			box-shadow: inset 0 -3px $color__border-blue;
			outline: none;
		}
	}

	&__tab {
		align-items: center;
		display: flex;
		flex-grow: 1;
		line-height: 1;
		justify-content: center;

		&--active {
			box-shadow: inset 0 -3px $color__border-blue;
			font-weight: 600;
		}
	}

	&--locked &__tab {
		pointer-events: none;
	}
}

.nab-resolution-selector {
	display: flex;
	justify-content: center;

	&__option {
		display: flex;
		align-items: center;
	}
}
